import axios from 'axios'
import $ from 'jquery'
function Wyc() {
    this.timer = null;
    if(location.hostname=='127.0.0.1'){
        this.environment = 'tem';
    }else{
        this.environment = 'pro';
        hostObj = getHost();
    }
}
var hostObj = {};
function getHost() {
    var hostNameObj = {
        'api': '',
        'att': '',
        'www': '',
        'class': '',
        'static': '',
    };
    var req = new XMLHttpRequest();
    req.open('GET', document.location, false);
    req.send(null);
    var str = req.getAllResponseHeaders();
    var arr = str.split('\r\n');
    var hostName = '';
    for (var i = 0; i < arr.length; i++) {
        if (arr[i].indexOf('x-deploy-hostname-config') > -1) {
            hostName = arr[i].split(': ')[1];
        }
    }
    $.ajax({
        async: false,
        xhrFields: {
            withCredentials: true
        },
        type: 'GET',
        data: {},
        url: '/channel/config/config.json',
        success: function (data) {
            if (hostName.length > 0 && data.hostname[hostName]) {
                changeHostName(data, 'api')
                changeHostName(data, 'att')
                changeHostName(data, 'www')
                changeHostName(data, 'class');
                changeHostName(data, 'static')
            } else {
                hostName = data.hostname.default;
                changeHostName(data, 'api')
                changeHostName(data, 'att')
                changeHostName(data, 'www')
                changeHostName(data, 'class')
                changeHostName(data, 'static')
            }
        },
        error: function (data) {
            console.log(data);
            alert('网络错误')
        }
    });
    function changeHostName(_data, _host) {
        if (_data.hostname[hostName][_host]) {
            if (_data.hostname[hostName][_host]['https']) {
                hostNameObj[_host] = _data.hostname[hostName][_host]['https']
            } else {
                hostNameObj[_host] = _data.hostname[hostName][_host]['http']
            }
        } else {
            if (_data.hostname[hostName]['_default']['https']) {
                hostNameObj[_host] = _data.hostname[hostName]['_default']['https']
            } else {
                hostNameObj[_host] = _data.hostname[hostName]['_default']['http']
            }

        }
    }

    return hostNameObj;
}
Wyc.prototype = {
    path: function () {
        var that = this;
        if (this.environment == 'dev') {
            return {
                endpoint: "../static/data",
                jsonpoint: '.json'
            }
        } else if (this.environment == 'pro') {
            return {
                endpoint: '',
                endpointWWW: hostObj.www,
                endpointATT: hostObj.att,
                endpointAPI: hostObj.api,
                endpointCLASS: hostObj.class,
                jsonpoint: ''
            }
        } else if (this.environment == 'tem') {
            return {
                endpoint: '',
                endpointWWW: '',
                endpointATT: 'https://att.i-school.net',
                endpointAPI: '',
                endpointCLASS: 'https://class.i-school.net',
                jsonpoint: ''
            }
        } else {
            console.log('开发环境类型错误')
        }
    },
    getRandomColor: function () {
        return '#' + (function (color) {
                return new Array(7 - color.length).join("0") + color
            })((Math.random() * 0x1000000 << 0).toString(16))
    },
    test: function () {
        window.alert(2)
    },
    tips: function (msg, t, fn) {
        var that = this;
        clearTimeout(that.timer);
        $('.tips-wrap').remove();
        var tips = $('<div class="tips-wrap"><span class="tips">' + msg + '</span></div>');
        $('body').append(tips);
        var t = t || 2000;
        tips.css({
            'position': 'fixed',
            'font-size': '14px',
            'top': 0,
            'left': 0,
            'width': '100%',
            'height': '100%',
            'z-index': '1000000',
            'display': 'none'
        });
        tips.find('.tips').css({
            'background': 'rgba(0,0,0,0.7)',
            'color': '#ffffff',
            'position': 'absolute',
            'top': '50%',
            'left': '50%',
            'transform': 'translate(-50%,-50%)',
            'padding': '15px',
            'border-radius': '10px',
            'text-align': 'center'
        });
        $('.tips-wrap').fadeIn();
        that.timer = setTimeout(function () {
            $('.tips-wrap').remove();
            fn && fn()
        }, t)
    },
    time: function (data, type) {
        var _Date = (data+'').length>10?data:parseInt(data + '000');
        var date = new Date(_Date),
            Y = date.getFullYear(),
            M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1),
            D = date.getDate() < 10 ? '0' + date.getDate() : date.getDate(),
            h = date.getHours() < 10 ? '0' + date.getHours() : date.getHours(),
            m = date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes(),
            s = date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds();
        if (type == 'year') {
            return Y
        } else if (type == 'month') {
            return M
        } else if (type == 'day') {
            return D
        } else if (type == 'ymd') {
            return Y + '-' + M + '-' + D
        } else if (type == 'time') {
            return h + ':' + m + ':' + s
        } else if (type == 'hm') {
            return h + ':' + m
        } else {
            return Y + '-' + M + '-' + D + ' ' + h + ':' + m + ':' + s
        }
    },
    hmTos:function (date,type) {
        let s = 0;
        if(type==1){
            let hour = date.split(':')[0];
            let min = date.split(':')[1];

            s = Number(hour*3600) + Number(min*60)
        }
        if(type==2) {
            let h = Math.floor(date / 3600) < 10 ? '0' + Math.floor(date / 3600) : Math.floor(date / 3600);
            let m = Math.floor((date / 60 % 60)) < 10 ? '0' + Math.floor((date / 60 % 60)) : Math.floor((date / 60 % 60));
            s = `${h}:${m}`;
        }
        return s
    },
    debounce: function (idle, action) {
        var last;
        return function () {
            var ctx = this, args = arguments
            clearTimeout(last)
            last = setTimeout(function () {
                console.log(1)
                action.apply(ctx, args)
            }, idle)
        }
    },
    throttle: function (delay, action) {
        var last = 0;
        return function () {
            var curr = +new Date();
            if (curr - last > delay) {
                action.apply(this, arguments);
                last = curr
            }
        }
    },
    //截取哈希值
    hashString: function () {
        var href = window.location.hash.replace(/^#/, '');
        //var obj = href.split("&")[_num_].split("=")[1];
        var part = href.split("&");
        var obj = {};
        for (var i = 0; i < part.length; i++) {
            var _part = part[i].split('=');
            obj[_part[0]] = _part[1]
        }
        return obj
    },
    //数字千分
    toThousands: function (num) {
        var num = (num || 0).toString(), result = '';
        while (num.length > 3) {
            result = ',' + num.slice(-3) + result;
            num = num.slice(0, num.length - 3);
        }
        if (num) {
            result = num + result;
        }
        return result;
    },
    urlString: function () {
        var href = window.location.search.replace(/^\?/, '');
        var part = href.split("&");
        var obj = {};
        for (var i = 0; i < part.length; i++) {
            var _part = part[i].split('=');
            obj[_part[0]] = _part[1]
        }
        return obj
    },
    urlPString: function () {
        if(document.referrer.indexOf('?')==-1){
            return {}
        }else {
            var href = document.referrer.split('?')[1];
            var part = href.split("&");
            var obj = {};
            for (var i = 0; i < part.length; i++) {
                var _part = part[i].split('=');
                obj[_part[0]] = _part[1]
            }
            return obj
        }
    },
    getCookie: function (name) {
        var strcookie = document.cookie;//获取cookie字符串
        var arrcookie = strcookie.split("; ");//分割
        //遍历匹配
        for (var i = 0; i < arrcookie.length; i++) {
            var arr = arrcookie[i].split("=");
            if (arr[0] == name) {
                return arr[1];
            }
        }
        return "";
    },
    //FullCalendar 日期格式化
    changeData: function (date,type,status){
        let str = '';
        let that = this;
        if(status==1){
            if(date.length>10){
                str = date.replace(/T/,' ').replace(/\+08:00/,'')
            }else{
                if(type=='end'){
                    var dateTime=new Date(date);
                    dateTime=dateTime.setDate(dateTime.getDate()-1);
                    str = that.time(dateTime,'ymd')
                }else{
                    str = date;
                }

            }
        }else{
            if(date.length>10){
                str = date.replace(/ /,'T')+'+08:00'
            }else{
                if(type=='end'){
                    var dateTime=new Date(date);
                    dateTime=dateTime.setDate(dateTime.getDate()+1);
                    str = that.time(dateTime,'ymd')
                }else{
                    str = date;
                }

            }
        }
        return str
    },
    price:function (rule, value, callback) {
        let ex = /^[0-9]*[1-9][0-9]*$/;
        setTimeout(() => {
            if (ex.test(value)||value.length==0) {
                callback();
            } else {
                callback(new Error('请输入正整数'));
            }
        }, 200);
    },
    /* 数据请求 */
    ajaxRequest: function (request, type, prams, callBack, failBack) {
        var arr = [];
        for (var name in prams) {
            arr.push(encodeURIComponent(name) + "=" + encodeURIComponent(prams[name]));
        }
        var str = arr.join('&');
        if (type == 'get') {
            axios(request + '?' + str)
                .then(function (response) {
                    if (callBack && response.data.errno == 0) {
                        callBack(response.data)
                    } else if (callBack && response.data.errno != 0) {
                        failBack(response.data)
                    }
                })
                .catch(function (error) {
                    console.log(error)
                });
        } else {
            axios({
                method: type,
                data: str,
                url: request
            })
                .then(function (response) {
                    if (callBack && response.data.errno == 0) {
                        callBack(response.data)
                    } else if (callBack && response.data.errno != 0) {
                        failBack(response.data)
                    }
                })
                .catch(function (error) {
                    console.log(error)
                });
        }
    }
};
var W = new Wyc();
export default W;
