import Vue from 'vue'
import Qs from 'qs'
import { Message, MessageBox } from 'element-ui'
import W from './public'

Vue.prototype.$message = Message
Vue.prototype.$alert = MessageBox

export default class Api {
  constructor (baseDrl) {
    this.base_url = baseDrl
  }

  setDate (date) {
    this.datetime = {
      date: date
    }
    return this
  }

  // 传参
  setData (data) {
    this.data = data
    return this
  }

  setDateRange (beginDate, endDate = null) {
    if (Array.isArray(beginDate) && beginDate.length === 2) {
      this.datetime = { begin_date: beginDate[0], end_date: beginDate[1] }
    } else if (typeof beginDate === 'string' && typeof endDate === 'string') {
      this.datetime = { begin_date: beginDate, end_date: endDate }
    }
    return this
  }
  // setDateRange (beginDate, endDate) {
  //   this.datetime = {
  //     begin_date: beginDate,
  //     end_date: endDate
  //   }
  //   return this
  // }

  mkDate (pos) {
    const d = new Date()
    d.setDate(d.getDate() + pos)
    return d.toLocaleDateString().replace(/\//g, '-')
  }

  getApiData (action, data, callback,errback) {
    const url = this.base_url + '/' + action
    const params = Object.assign({}, data)
    for (const key in this.datetime) {
      params[key] = this.datetime[key]
    }
    Vue.prototype.$http.post( W.path().endpointAPI + url, Qs.stringify(params)).then(res => {
      const response = res.data
      if (response.errno === 0) {
        if (response.data) {
          callback && callback(response.data)
        } else {
        }
      } else {
        console.error(response.error)
        if(errback){
          errback(response.error)
        }
       
        Message.warning({ message: response.error, duration: 2000, showClose: true, type: 'error', center: true })
      }
    })
  }
}
